
.array-container {
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: flex-end;
   width: 650px;                   
   height: 60%;
   background-color: lightgray;
   margin-top: 20px;              
}

.button-bar {
   width: 100%;
   max-width: 800px;             
   height: 50px;
   display: flex;
   justify-content: space-around;
   align-items: center;
   margin-bottom: 20px;   
   font-size: 30px;
   
}
.button-bar button {
   width: 20%;
   height: 50px;
   background: rgb(216, 194, 160);
   border: none;
   cursor: pointer;
   transition: background 0.3s;
   font-size: 20px;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.button-bar button:hover {
   background: rgb(223, 126, 0);
}

.button-bar p{
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}

.array-bar {
   width: 3%;                  
   background-color: cadetblue;
   margin: 0 2px;                
}

.component-section {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   height: 69vh;
}
